import Vue from 'vue'
import VueRouter from 'vue-router'
import iroot from '../root/root.config.js'

Vue.use(VueRouter)
    // 0.   Vue.use(VueRouter)

// 1. 
//  import 


import Home from './../../src/components/Home.vue'
import WebStorage from './../../src/components/WebStorage.vue'
import History from './../../src/components/History.vue'
import WebWorkers from './../../src/components/WebWorkers.vue'
import DragAndDrop from './../../src/components/DragAndDrop.vue'
import File from './../../src/components/File.vue'
import Geolocation from './../../src/components/Geolocation.vue'
import Audio from './../../src/components/Audio.vue'
import Video from './../../src/components/Video.vue'
import Canvas2d from './../../src/components/Canvas2d.vue'
import Cache from './../../src/components/Cache.vue'
import IndexedDB from './../../src/components/IndexedDB.vue'

// 2. 
//  "component" 
//  Vue.extend() 
// 
// 
const routes = [
    { path: `${iroot}home`, component: Home },
    { path: `${iroot}webstorage`, component: WebStorage },
    { path: `${iroot}history`, component: History },
    { path: `${iroot}webworkers`, component: WebWorkers },
    { path: `${iroot}draganddrop`, component: DragAndDrop },
    { path: `${iroot}file`, component: File },
    { path: `${iroot}geolocation`, component: Geolocation },
    { path: `${iroot}audio`, component: Audio },
    { path: `${iroot}video`, component: Video },
    { path: `${iroot}canvas2d`, component: Canvas2d },
    { path: `${iroot}cache`, component: Cache },
    { path: `${iroot}indexeddb`, component: IndexedDB },
    { path: '*', redirect: `${iroot}home` },
]

// 3.  router  `routes` 
// , 
const router = new VueRouter({
    mode: 'history',
    routes //  routes: routes
})

export default router
