<template>
    <div v-show="show" class="api-menu">
        <ul>
            <li v-for="(item,index) of menus" :key="index" @click="menuclick">
                <router-link :to="item.link | lowercase">{{item.title}}</router-link>
            </li>
        </ul>
    </div>
</template>
<script>
import iroot from '../../config/root/root.config.js'
export default {
    data() {
            return {
                show: false,
                menus: [{
                    title: 'Home',
                    link: `${iroot}home`,
                    selected: true
                }, {
                    title: 'WebStorage',
                    link: `${iroot}WebStorage`,
                    selected: false
                }, {
                    title: 'History',
                    link: `${iroot}History`,
                    selected: false
                }, {
                    title: 'WebWorkers',
                    link: `${iroot}WebWorkers`,
                    selected: false
                }, {
                    title: 'Drag and Drop',
                    link: `${iroot}DragAndDrop`,
                    selected: false
                }, {
                    title: 'File',
                    link: `${iroot}File`,
                    selected: false
                }, {
                    title: 'Geolocation',
                    link: `${iroot}Geolocation`,
                    selected: false
                }, {
                    title: 'Audio',
                    link: `${iroot}Audio`,
                    selected: false
                }, {
                    title: 'Video',
                    link: `${iroot}Video`,
                    selected: false
                }, {
                    title: 'Canvas2d',
                    link: `${iroot}Canvas2d`,
                    selected: false
                }, {
                    title: 'Cache',
                    link: `${iroot}Cache`,
                    selected: false
                }, {
                    title: 'IndexedDB',
                    link: `${iroot}IndexedDB`,
                    selected: false
                }]
            }
        },
        methods: {
            menuclick() {
                this.show = false;
                document.documentElement.scrollTop = document.body.scrollTop = 0;
                this.$emit('on-click');
            }
        }
}
</script>
<style>
.api-menu {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 10001;
    background: rgba(255, 255, 255, 0.8);
    text-align: center;
}

.api-menu ul {
    height: 100%;
    display: flex;
    flex-direction: column;
}

.api-menu li {
    border: 1px solid #42B9BA;
    margin-top: -1px;
    flex: 1;
    display: flex;
    align-items: center;
}

.api-menu li a {
    display: block;
    flex: 1;
    padding: 5px;
    text-align: center;
}

.api-menu li a:hover {
    font-weight: bold;
}

.router-link-active {
    color: #f60;
}
</style>



