<template>
    <div id="app">
        <i-header></i-header>
        <i-menu v-show="showMenu" @on-click="closeMenu"></i-menu>
        <div class="api-box" :class="apiBoxClass">
            <transition enter-active-class="api-pos animated bounceInLeft" leave-active-class="api-pos animated rotateOutDownLeft">
                <router-view></router-view>
            </transition>
        </div>
        <i-back-top></i-back-top>
        <i-menu-btn @on-click="openMenu" v-show="showMenuBtn"></i-menu-btn>
    </div>
</template>
<script>
import iMenu from './Menu.vue'
import iHeader from './Header.vue'
import iMenuBtn from './MenuBtn.vue'
import iBackTop from './BackTop.vue'

export default {
    components: {
        iMenu,
        iHeader,
        iMenuBtn,
        iBackTop
    },
    name: 'app',
    data() {
        return {
            showMenu: false,
            showMenuBtn: true,
            apiBoxClass: {
                'api-blur': false
            }
        }
    },
    methods: {
        openMenu() {
            this.showMenu = true;
            this.showMenuBtn = false;
            this.apiBoxClass['api-blur'] = true;
        },
        closeMenu() {
            this.showMenu = false;
            this.showMenuBtn = true;
            this.apiBoxClass['api-blur'] = false;
        }
    }
}
</script>
<style>
* {
    margin: 0;
    padding: 0;
}

html {
    background: #fff;
    overflow-x: hidden;
}

#app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
    line-height: 1.5;
}

ul,
li,
ol {
    list-style: none;
}

h1 {
    padding: 20px;
    text-align: center;
}

h2 {
    padding: 10px;
    background: #eee;
    margin-bottom: 20px;
}

a {
    color: #42b983;
    text-decoration: none;
}

.api {
    background: #fff;
    margin: 0 10px;
    overflow: hidden;
}

.api-header h1 {
    font-size: 20px;
}

.api-pos {
    position: absolute;
    width: 100%;
}

.api-info {
    margin-bottom: 30px;
}

.api-info h3 {
    padding: 5px 0;
    border-bottom: 1px solid #eee;
    margin-bottom: 10px;
}


/*api-box*/

.api-box {
    position: relative;
}

.api-blur {
    -webkit-filter: blur(5px);
    -moz-filter: blur(5px);
    filter: blur(5px);
}


/*api-code*/

.api-code {
    text-indent: 0;
    overflow-x: hidden;
    background: rgba(0, 0, 0, 0.8);
    border-radius: 5px;
    margin: 30px 0;
    color: #fff;
    overflow-x: auto;
}

.api-red {
    color: red;
}

.api-green {
    color: #42b983;
}


/*api-table*/

.api-table {
    width: 100%;
    margin-bottom: 30px;
}

.api-table td {
    padding: 10px 15px;
}

.api-table tr:nth-child(even) {
    background: #eee;
}

.api-table-caption {
    border-bottom: 1px solid #eee;
}

.api-text-center {
    text-align: center;
}

.api-mark {
    text-align: right;
    color: #ddd;
}

.api-menu-btn,
.api-back-top {
    position: fixed;
    z-index: 1000;
    width: 50px;
    height: 50px;
    cursor: pointer;
    text-align: center;
    line-height: 50px;
    border-radius: 5px;
    color: #fff;
    background-color: rgba(0, 0, 0, 0.8);
}

.api-menu-btn {
    bottom: auto;
}
</style>



