<template>
    <div v-show="show" :style="styles" @click="backtop" class="api-back-top">Top</div>
</template>
<script>
export default {
    name: 'canvas2d',
    props: {
        right: {
            type: Number,
            default: 20
        },
        bottom: {
            type: Number,
            default: 20
        }
    },
    data() {
        return {
            show: false,
            isSystem: false,
            timer: null
        }
    },
    computed: {
        styles() {
            return `right: ${this.right}px; bottom: ${this.bottom}px`;
        }
    },
    mounted() {
        window.addEventListener('scroll', function() {
            this.show = (this.getScrollTop() != 0);
            if (this.isSystem) {
                clearInterval(this.timer);
                this.timer = null;
            } else {
                this.isSystem = true;
            }
        }.bind(this), false)
    },
    methods: {
        getScrollTop() {
            return document.documentElement.scrollTop || document.body.scrollTop;
        },
        setScrollTop(num) {
            num = num || 0;
            document.documentElement.scrollTop = document.body.scrollTop = num;
        },
        backtop() {
            this.timer = setInterval(function() {
                var top = this.getScrollTop();
                if (top == 0) {
                    clearInterval(this.timer);
                    this.timer = null;
                } else {
                    this.setScrollTop(top / 1.2);
                    this.isSystem = false;
                }
            }.bind(this), 1000 / 60);

            this.$emit('on-click');
        }
    }
}
</script>



