<template>
    <div class="api">
        <h2>Canvas2D API</h2>
        <div class="api-info">
            <h3>Canvas2D API</h3>
            <p>Canvas is a part of the new HTML5 specification for next generation web technologies. The HTML5 canvas is an HTML tag that you can embed inside an HTML document for the purpose of drawing graphics with JavaScript. Since the HTML5 canvas is a bitmap, every pixel drawn onto the canvas overrides pixels beneath it.</p>
        </div>
        <div class="api-info">
            <h3>The Canvas tag</h3>
        </div>
        <pre class="api-code">
        &lt;canvas id="canvas" width="578" height="200"&gt;&lt;/canvas&gt;
    </pre>
        <div class="api-info">
            <h3>Context</h3>
            <p>&lt;canvas&gt; creates a fixed size drawing surface that exposes one or more rendering contexts, which are used to create and manipulate the content shown. We'll focus on the 2D rendering context.</p>
        </div>
        <pre class="api-code">
    var canvas = document.getElementById("canvas"); 
    var context = canvas.getContext("2d");
    </pre>
        <h4 class="api-table-caption">The Methods</h4>
        <table class="api-table">
            <tr>
                <td>save()</td>
                <td>This method saves the canvas state, including the transformation matrix, styling properties and the clipping mask.</td>
            </tr>
            <tr>
                <td>restore()</td>
                <td>This method restores the last state saved, including the transformation matrix, styling properties and the clipping mask.</td>
            </tr>
        </table>
        <pre class="api-code">
    context.save(); 
    context.restore();
    </pre>
        <div class="api-info">
            <h3>Rectangles</h3>
        </div>
        <h4 class="api-table-caption">The Methods</h4>
        <table class="api-table">
            <tr>
                <td>fillRect(x, y, width, height)</td>
                <td>This method will draw a solid rectangle directly to the canvas in the position x,y and the size width,height.</td>
            </tr>
            <tr>
                <td>strokeRect(x, y, width, height)</td>
                <td>This method will draw an outline rectangle directly to the canvas in the position x,y and the size width,height.</td>
            </tr>
            <tr>
                <td>clearRect(x, y, width, height)</td>
                <td>This method clears the area in the canvas using a rectangular shape declared by the value of its attributes.</td>
            </tr>
        </table>
        <pre class="api-code">
    context.fillRect(0,0,100,100); 
    context.clearRect(25,25,60,60); 
    context.strokeRect(50,50,50,50);
    </pre>
        <div class="api-info">
            <h3>Paths</h3>
        </div>
        <h4 class="api-table-caption">The Methods</h4>
        <table class="api-table">
            <tr>
                <td>beginPath()</td>
                <td>This method is required to start a new path.</td>
            </tr>
            <tr>
                <td>closePath()</td>
                <td>This method can be used at the end of a path to close it. It will generate a straight line from the last position of the pen to the point from which the path started. Its not necessary to use this method when the path must remain open or it is drawn onto the canvas using fill().</td>
            </tr>
            <tr>
                <td>rect(x, y, width, height)</td>
                <td>This method adds a rectangle to the path in the position x,y and with the size width,height.</td>
            </tr>
            <tr>
                <td>stroke()</td>
                <td>This method is used to draw the path as an outline shape.</td>
            </tr>
            <tr>
                <td>fill()</td>
                <td>This method is used to draw the path as a solid shape.</td>
            </tr>
            <tr>
                <td>clip()</td>
                <td>This method is used to create a new clipping area defined by the path. Everything sent to the canvas after this method is declared will be drawn only if it falls inside the shape.</td>
            </tr>
            <tr>
                <td>moveTo(x, y)</td>
                <td>This method moves the virtual pen to a new position. The next method will continue the path from that point.</td>
            </tr>
        </table>
        <pre class="api-code">
    context.beginPath(); 
    context.moveTo(75,50); 
    context.lineTo(100,75); 
    context.lineTo(45,125); 
    context.closePath(); 
    context.stroke(); // or context.fill(); 

    context.rect(10, 10, 80, 90);
    </pre>
        <div class="api-info">
            <h3>Lines</h3>
        </div>
        <h4 class="api-table-caption">The Methods</h4>
        <table class="api-table">
            <tr>
                <td>lineTo(x, y)</td>
                <td>This method adds a straight line to the path from the current pens position to the point indicated by the attributes x and y.</td>
            </tr>
        </table>
        <pre class="api-code">
    context.beginPath(); 
    context.moveTo(75,50); 
    context.lineTo(100,75); 
    context.lineTo(45,125); 
    context.closePath();
    </pre>
        <h4 class="api-table-caption">The Properties</h4>
        <table class="api-table">
            <tr>
                <td>lineWidth</td>
                <td>This property sets the line thickness. By default, the value is 1.0.</td>
            </tr>
            <tr>
                <td>lineCap</td>
                <td>This property determines the shape of the ending of the lines. There are three possible values: butt (normal ending), round (ending the line with a semicircle) and square (ending the line with a square).</td>
            </tr>
            <tr>
                <td>lineJoin</td>
                <td>This property determines the shape of the connection between two lines. It takes three values: round (where the join is rounded), bevel (where the join is cut) and miter (where the join is extended until both lines reach a single point).</td>
            </tr>
            <tr>
                <td>miterLimit</td>
                <td>This property takes a number to determine how long the lines will be extended when the lineJoin property is set to miter.</td>
            </tr>
        </table>
        <pre class="api-code">
    context.lineWidth=6; 
    context.lineCap='round'; 
    context.lineJoin='miter'; 
    context.miterLimit=10.0; 
    </pre>
        <div class="api-info">
            <h3>Arcs</h3>
        </div>
        <h4 class="api-table-caption">The Methods</h4>
        <table class="api-table">
            <tr>
                <td>arc(x, y, radius, startAngle, endAngle, direction)</td>
                <td>This method adds an arc to the path. The center of the arc is determined by x and y, the angles are declared in radians and direction is a boolean value for clockwise or anticlockwise direction. To convert degrees into radians, use the formula: Math.PI/180degrees.</td>
            </tr>
        </table>
        <pre class="api-code">
    context.arc(30, 30, 20, 0, Math.PI, true);  
    </pre>
        <div class="api-info">
            <h3>Bezier and quadratic curves</h3>
        </div>
        <h4 class="api-table-caption">The Methods</h4>
        <table class="api-table">
            <tr>
                <td>quadraticCurveTo(cpx, cpy, x, y)</td>
                <td>This method adds a quadratic Bzier curve to the path. It starts from the current pens position and ends at x,y. The cpx and cpy attributes specify the position of the control point that will shape the curve.</td>
            </tr>
            <tr>
                <td>bezierCurveTo(cp1x, cp1y, cp2x, cp2y, x, y)</td>
                <td>This method adds a cubic Bzier curve to the path. It starts from the current pens position and ends at x,y. The cp1x, cp1y, cp2x, and cp2y attributes specify the position of the two control points that will shape the curve.</td>
            </tr>
        </table>
        <pre class="api-code">
    context.beginPath();
    context.moveTo(50,50);
    context.quadraticCurveTo(40,40,25,75);
    context.quadraticCurveTo(125,25,75,25);
    context.stroke();

    context.beginPath();
    context.moveTo(50,50);
    context.bezierCurveTo(75,37,70,25,50,25);
    context.bezierCurveTo(20,25,20,62.5,20,62.5);
    context.stroke();
    </pre>
        <div class="api-info">
            <h3>Images</h3>
        </div>
        <h4 class="api-table-caption">The Methods</h4>
        <table class="api-table">
            <tr>
                <td>drawImage()</td>
                <td>This method will draw an image on the canvas. There are three possible syntaxes.</td>
            </tr>
            <tr>
                <td>drawImage(image, x, y)</td>
                <td>simple draw</td>
            </tr>
            <tr>
                <td>drawImage(image, x, y, width, height)</td>
                <td>draw with scaling</td>
            </tr>
            <tr>
                <td>drawImage(img, sx, sy, swidth, sheight, x, y, width, height)</td>
                <td>draw with slicing</td>
            </tr>
            <tr>
                <td>getImageData(x, y, width, height)</td>
                <td>This method gets a portion of canvas and saves it as an object. The values of the object are accessible through the properties width, height and data. The first two properties return the size of the portion of the image taken, and data returns the information as an array with values representing the colors of the pixels. This values can be accessed using the formula (width4y)+(x4).</td>
            </tr>
            <tr>
                <td>putImageData(imagedata, x, y)</td>
                <td>This method draws the image represented by the information in imagedata onto the canvas.</td>
            </tr>
            <tr>
                <td>createImageData(width, height)</td>
                <td>This method creates a new image in data format. All the pixels are initially transparent black. It can take image data as an attribute instead of width and height. In this case the new image will have the size determined by the data provided.</td>
            </tr>
            <tr>
                <td>createPattern(image, type)</td>
                <td>This method creates a pattern from an image that later can be assigned to a shape as a color using the fillStyle property. The possible values for the type attribute are repeat, repeat-x, repeat-y and no-repeat.</td>
            </tr>
        </table>
        <pre class="api-code">
    var img = new Image(src); 
    context.drawImage(img,0,0);
    context.drawImage(img,0,0,100,80);
    context.drawImage(img,33,71,104,124,21,20,87,104);
    </pre>
        <h4 class="api-table-caption">The Properties</h4>
        <table class="api-table">
            <tr>
                <td>img</td>
                <td>Specifies the image, canvas, or video element to use</td>
            </tr>
            <tr>
                <td>sx</td>
                <td>Optional. The x coordinate where to start clipping</td>
            </tr>
            <tr>
                <td>sy</td>
                <td>Optional. The y coordinate where to start clipping</td>
            </tr>
            <tr>
                <td>swidth</td>
                <td>Optional. The width of the clipped image</td>
            </tr>
            <tr>
                <td>sheight</td>
                <td>Optional. The height of the clipped image</td>
            </tr>
            <tr>
                <td>x</td>
                <td>The x coordinate where to place the image on the canvas</td>
            </tr>
            <tr>
                <td>y</td>
                <td>The y coordinate where to place the image on the canvas</td>
            </tr>
            <tr>
                <td>width</td>
                <td>Optional. The width of the image to use (stretch or reduce the image)</td>
            </tr>
            <tr>
                <td>height</td>
                <td>Optional. The height of the image to use (stretch or reduce the image)</td>
            </tr>
        </table>
        <div class="api-info">
            <h3>Colors</h3>
        </div>
        <h4 class="api-table-caption">The Properties</h4>
        <table class="api-table">
            <tr>
                <td>strokeStyle</td>
                <td>This property declares the color for the lines of the shapes. It can take any CSS value, including functions such as rgb() and rgba().</td>
            </tr>
            <tr>
                <td>fillStyle</td>
                <td>This property declares the color for the interior of solid shapes. It can take any CSS value, including the functions rgb() and rgba(). Its also used to assign gradients and patterns to shapes (These styles are first assigned to a variable and then the variable is assigned as a color to this property.).</td>
            </tr>
            <tr>
                <td>globalAlpha</td>
                <td>This property is used to set the level of transparency for every shape. It takes values from 0.0 (fully opaque) to 1.0 (fully transparent).</td>
            </tr>
        </table>
        <pre class="api-code">
    context.strokeStyle = "#FFA500"; 
    context.fillStyle = "#FFA500"; 
    context.globalAlpha=1;
    </pre>
        <div class="api-info">
            <h3>Gradients</h3>
        </div>
        <h4 class="api-table-cation">The Methods</h4>
        <table class="api-table">
            <tr>
                <td>createLinearGradient(x1, y1, x2, y2)</td>
                <td>This method creates a linear gradient to be assigned to a shape as a color using the fillStyle property. Its attributes only specify the start and end positions (relative to the canvas). To declare the colors involved in the gradient, this method must be used in conjunction with addColorStop().</td>
            </tr>
            <tr>
                <td>createRadialGradient(x1, y1, r1, x2, y2, r2)</td>
                <td>This method creates a radial gradient to be assigned to a shape as a color using the fillStyle property. The gradient is built from two circles. The attributes only specify the position and radius of the circles (relative to the canvas). To declare the colors involved in the gradient, this method must be used in conjunction with addColorStop().</td>
            </tr>
            <tr>
                <td>addColorStop(position, color)</td>
                <td>This method is used to declare the colors for gradients. The attribute position is a value between 0.0 and 1.0, used to determine where the color will start degrading.</td>
            </tr>
        </table>
        <pre class="api-code">
    var lingrad = context.createLinearGradient(0,0,0,150); 
    lingrad.addColorStop(0, '#00ABEB'); 
    lingrad.addColorStop(0.5, '#fff'); 
    lingrad.addColorStop(0.5, '#26C000'); 
    lingrad.addColorStop(1, '#fff'); 

    var radgrad = ctx.createRadialGradient(45,45,10,52,50,30); 
    radgrad.addColorStop(0, '#A7D30C'); 
    radgrad.addColorStop(0.9, '#019F62'); 
    radgrad.addColorStop(1, 'rgba(1,159,98,0)');
    </pre>
        <div class="api-info">
            <h3>Shadows</h3>
        </div>
        <h4 class="api-table-caption">The Properties</h4>
        <table class="api-table">
            <tr>
                <td>shadowColor</td>
                <td>This property sets the color for a shadow. It takes CSS values.</td>
            </tr>
            <tr>
                <td>shadowOffsetX</td>
                <td>This property declares how many units far the shadow will be from the object in the horizontal direction.</td>
            </tr>
            <tr>
                <td>shadowOffsetY</td>
                <td>This property declares how many units far the shadow will be from the object in the vertical direction.</td>
            </tr>
            <tr>
                <td>shadowBlur</td>
                <td>This property takes a number to produce a blurring effect for a shadow.</td>
            </tr>
        </table>
        <pre class="api-code">
    context.shadowOffsetX = 1; 
    context.shadowOffsetY = 1; 
    context.shadowBlur = 3; 
    context.shadowColor = "rgba(255, 0, 0, 0.5)";
    </pre>
        <div class="api-info">
            <h3>Translating</h3>
        </div>
        <h4 class="api-table-caption">The Methods</h4>
        <table class="api-table">
            <tr>
                <td>translate(x, y)</td>
                <td>This method moves the canvas origin to the point x,y. The initial position of the origin (0,0) is the top-left corner of the area generated by the
                    <canvas> element.</td>
            </tr>
        </table>
        <pre class="api-code">
    context.translate(100, 100);
    </pre>
        <div class="api-info">
            <h3>Rotating</h3>
        </div>
        <h4 class="api-table-caption">The Methods</h4>
        <table class="api-table">
            <tr>
                <td>rotate(angle)</td>
                <td>This method is used to rotate the canvas over the origin. The angle must be declared in radians. To convert degrees into radians, use the formula: Math.PI/180degrees.</td>
            </tr>
        </table>
        <pre class="api-code">
    context.rotate(Math.PI*2);
    </pre>
        <div class="api-info">
            <h3>Scaling</h3>
        </div>
        <h4 class="api-table-caption">The Methods</h4>
        <table class="api-table">
            <tr>
                <td>scale(x, y)</td>
                <td>This method changes the scale of the canvas. The values by default are (1.0,1.0). The values can be negative.</td>
            </tr>
        </table>
        <pre class="api-code">
    context.scale(0.6,0.6);
    </pre>
        <div class="api-info">
            <h3>Transforms</h3>
        </div>
        <h4 class="api-table-caption">The Methods</h4>
        <table class="api-table">
            <tr>
                <td>transform(m1, m2, m3, m4, dx, dy)</td>
                <td>This method modifies the transformation matrix of the canvas. The new matrix is calculated over the previous one.</td>
            </tr>
            <tr>
                <td>setTransform(m1, m2, m3, m4, dx, dy)</td>
                <td>This method modifies the transformation matrix of the canvas. It resets the previous values and declares the new ones.</td>
            </tr>
        </table>
        <pre class="api-code">
    context.transform(1,0.5,-0.5,1,30,10);
    context.setTransform(0.866, -0.5, 0.866, 0.5, 0, 0);
    </pre>
        <div class="api-info">
            <h3>Compositing</h3>
        </div>
        <h4 class="api-table-caption">The Properties</h4>
        <table class="api-table">
            <tr>
                <td>
                    globalCompositeOperation</td>
                <td>This property defines the composite operation between the source and destination states of the canvas.</td>
            </tr>
        </table>
        <div class="api-info">
            <img :src="source" height="418" width="549">
        </div>
        <pre class="api-code">
    context.globalCompositeOperation='source-over;
    </pre>
    </div>
</template>
<script>
import iroot from '../../config/root/root.config.js'
export default {
    name: 'canvas2d',
    data() {
        return {
            source: `${iroot}/src/assets/source.png`
        }
    }
}
</script>



