<template>
    <div v-show="show" :style="styles" @click="openMenu" class="api-menu-btn">Menu</div>
</template>
<script>
export default {
    name: 'canvas2d',
    props: {
        right: {
            type: Number,
            default: 20
        },
        bottom: {
            type: Number,
            default: 70
        }
    },
    data() {
        return {
            show: true
        }
    },
    computed: {
        styles() {
            return `right: ${this.right}px; bottom: ${this.bottom}px`;
        }
    },
    methods: {
        openMenu() {
            this.$emit('on-click');
        }
    }
}
</script>



