import Vue from 'vue'

/**
 * []
 */
Vue.filter('lowercase', function(input) {
    return input.toLowerCase();
});

/**
 * []
 */
Vue.filter('uppercase', function(input) {
    return input.toUpperCase();
});

/**
 * []
 */
Vue.filter('firstupper', function(input) {
    if (!input || input.length == 0) return false;
    return input.charAt(0) + input.substring(1);
});




